<template>
  <div id="app">
      <div class="head">
         <router-link to="/login">登录</router-link>
         <!-- <router-link to="/about">关于</router-link> -->
         <router-link to="/">主页</router-link>
         <div id="so"><input type="text" v-model="input"><div @click="so(input)"><img src="./assets/all_little.png" alt=""></div></div> 
      </div>
      <router-view></router-view>
  </div>
</template>

<script>
// import HelloWorld from './components/HelloWorld.vue'

export default {
  name: 'app',
  data(){
     return {
       input:""
     }
  },
  methods:{
     so(value){

              let so_value = value ? value : alert("输入值不可为空")  ;
              let url = "http://localhost:8081/getMess?" +so_value;
              
              let fetch_get = fetch(url);
              
              fetch_get
              .then((data)=>{return data.json();})
              .then((data)=>{
                //                    (data);
                 let result = [];
                 for(let i = 0; i < data.length;i++){
                   let newobj = {};
                   for (const key in data[i]) {
                      if(data[i]["type"] == "电影"){
                        newobj = data[i];
                      } 
                      let reg = new RegExp(data[i]["type"] + data[i]["title"] + "第"); 
                      if(reg.test(key.toString()) && key.toString().indexOf("...") == -1){
                        //                   (data[i].key);
                        newobj[key] = data[i][key.toString()];
                      }
                      newobj["imgUrl"] = data[i]["imgUrl"];
                      newobj["title"] = data[i]["title"];
                      newobj["type"] = data[i]["type"];
                      newobj["year"] = data[i]["year"];
                   }
                   result.push(newobj);
                 }  
                //                    (result);  
                 this.$store.state.result = result;
                //                    ("检测"); 
                //                    (this.$store.state);
                 this.$router.push({name:'About'});
                 return result;   
                //  let iframe = document.createElement("iframe");
             
               });
      },
  },
  components: {
   
  }
}
</script>

